.visually-hidden:not(:focus):not(:active) {
  /*Позиционирование*/
  position: absolute;
  /*Блочная модель*/
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  /*Разное*/
  white-space: nowrap;
  clip-path: inset(100%);
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}
@font-face {
  font-weight: normal;
  font-family: "PTSans";
